.t-wrapper{
    padding: 0 3rem 0 3rem;
    height: 100vh;
    /* display: flex; */
    align-items: center;
    justify-content: center;
    /* margin-top: 7rem;
    flex-direction: column; */
    gap: 2rem;
    position: relative;
}
.t-heading{
    text-align: center;
}
.t-heading>*{
    font-size: 2rem;
    font-weight: bold;
}
.t-heading span:nth-of-type(2){
    color: var(--orange);
}
.t-blur1{
    left: 38rem;
    top: 10rem;
}
.t-blur2{
    top: 5rem;
    left: 0;
}
.proj{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    padding: 2rem;
    width: 30rem;
    height: 12rem;
    background-color: rgb(255, 255,255,0.26);
    border: 7px solid var(--blueCard);
    box-shadow: var(--boxShadow);
    border-radius: 20px;
}
.proj>a>img{
    width: 6rem;
    height: 6rem;
}
.proj>span{
    color: var(--gray);
}
.t-wrapper .swiper{
    width: 100%;
    height: 70%;
}
.t-wrapper .swiper-slide{
    display: flex;
    align-items: center;
    justify-content: center;
}
.t-wrapper .swiper-pagination-bullet-active{
    background-color: var(--orange);
}
.t-wrapper .swiper-pagination-bullet{
    border: solid 3px var(--blueCard);
}
@media screen  and (max-width:480px) {
    .t-wrapper{
        padding: 0;
        height: 30rem;
    }
    .proj>a>img{
        width: 70px;
        height: 70px;
    }
}