.works{
    padding: 0 3rem 0 3rem;
    display: flex;
    height: 90vh;
    margin-top: 4rem;
}
.w-right{
    position: relative;
}
.w-mainCircle{
    left: 9rem;
    position: relative;
    width: 18rem;
    height: 18rem;
    border-radius: 100%;
    box-shadow: var(--smboxShadow);
    top: 2rem;
    background: white;
    z-index: 1;
}
.w-mainCircle>*{
    position: absolute;
}
.w-mainCircle>:nth-child(1){
    top: -3rem;
    left: 6rem;
}
.w-mainCircle>:nth-child(2){
    top: 6rem;
    left: -3rem;
}
.w-mainCircle>:nth-child(3){
    top: 6rem;
    left: 5rem;
}
.w-mainCircle>:nth-child(4){
    top: 15rem;
    left: 5rem;
}
.w-mainCircle>:nth-child(5){
    top: 6rem;
    left: 14rem;
}
.w-mainCircle>:nth-child(6){
    top: -3rem;
    left: 17rem;
    transform: scale(0.6);
}
.w-mainCircle>:nth-child(7){
    top: 14rem ;
    left: 17rem ;
    transform: scale(0.6);
}
.w-secCircle{
    width: 6rem;
    height: 6rem;
    position: absolute;
    border-radius: 100%;
    border: 5px solid #ebebeb;
    display: flex;
    align-items: center;
    justify-content: center;
    background: none;
    box-shadow: var(--smboxShadow);
    background: white;
    z-index: 2;
}
.w-secCircle>img{
    transform: scale(0.18);
}
.w-backCircle{
    position: absolute;
    height: 13rem;
    width: 13rem;
    border-radius: 100%;

}
.blueCircle{
    background: #194988;
    left: 18rem;
    top:0rem;
}
.yellowCircle{
    background: #F5C32C;
    left: 18rem;
    top: 8rem;
}
@media screen and (max-width:480px) {
    .works{
        flex-direction: column;
        height: 58rem;
        gap: 8rem;
        padding: 0;
    }
    .awesome{
        width: 100%;
        height: 30rem;
    }
    .w-right{
        display: flex;
        align-items: flex-start;
        justify-content: start;
        transform: scale(0.6);
        left: -2rem;
    }
    .w-mainCircle{
        position: static;
    }
   
}